.header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 20px;
  height: 14px;

  .menu {
    display: flex;
    align-items: center;
    a{
      color: #000;
      text-decoration: none;
    }
    div {
      padding: 12px;
      height: 18px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 4px;
      margin-left: 24px;
    }

    div:hover {
      background-color: rgb(231, 245, 255);
    }

    .active {
      background-color: rgb(231, 245, 255);
    }
  }


}