table {
  border: 1px solid #000;

  th {
    border: 1px solid #000;
  }

  tr {
    td {
      border: 1px solid #000;
    }
  }
}

.manual-box {
  padding: 24px;

  .title {
    margin-top: 24px;
    font-size: 32px;
    font-weight: bold;
    text-align: left;
  }

  .connect-wallet {
    cursor: pointer;
    border: 1px solid #203B60;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 42px;
    border-radius: 4px;
    margin-left: 24px;
  }

  .step-tab {
    display: flex;
    align-items: center;
    margin-top: 24px;

    div {
      padding: 12px;
      height: 18px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 4px;
      margin-left: 24px;
      cursor: pointer;
    }

    div:hover {
      background-color: rgb(231, 245, 255);
    }

    .active {
      background-color: rgb(231, 245, 255);
    }
  }

  .step-content {
    margin-left: 24px;

    .trigger-title {
      margin-top: 24px;
      font-size: 32px;
      font-weight: 600;
    }

    .action-step {
      margin-top: 24px;

      &-title {
        font-size: 20px;
        margin-bottom: 12px;
      }

      &-content {
        display: flex;
        align-items: center;

        .submit-input {
          select {
            margin: 0 8px;
          }
          .d-select-sub{
            margin-left: 8px;
          }
        }

        .chainList {
          margin-left: 8px;

          .request-network-type {
            display: flex;
            align-items: center;
            cursor: pointer;
            position: relative;

            .open-select {
              margin-left: 24px;
            }

            .request-type-network-text {
              margin-left: 12px;
              font-weight: 600;


              .request-network-text {
                font-size: 24px;
              }
            }

            .network-logo {
              img {
                width: 30px;
                height: 30px;
              }
            }

            .select-request-network {
              position: absolute;
              width: 180px;
              min-height: 156px;
              top: 42px;
              border-radius: 6px;
              background: #203B60;
              padding-top: 0;
              display: inline-block;



              .request-network {
                display: flex;
                align-items: center;
                padding: 12px;
                font-size: 20px;
                font-weight: 600;


                span {
                  margin-left: 12px;
                }

                .dowm-icon {
                  margin-left: 24px;
                }


              }

              .request-network:hover {
                background-color: #5577FD1A;
              }
            }
          }
        }

      }

      .parameter-call {
        margin-top: 24px;

        .param-info {
          margin-top: 12px;
          display: flex;
          align-items: center;

          .param-num {
            margin:0 8px;
          }
        
        }
      }
      button{
        margin-left: 100px;
        margin-top: 12px;
      }
    }
  }

  .info-card {
    margin-top: 24px;

    .info-data {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #eee;
      margin-top: 8px;
      .info-data-title{
        margin-right: 12px;
      }
    }
    button{
      margin-top: 8px;
      margin-bottom: 8px;
    }
  }
}